<template>
  <div>
    <Appbar />
    <v-main style="background-color: #0d1117">
      <v-container class="pt-4 px-auto mx-auto w-100 h-100 d-flex justify-center align-center" style="height: calc(100vh - 100px)">
        <v-btn color="#272727" class="ma-2 white--text" elevation="6" @click="redirect()" large>Login with GitHub</v-btn>
      </v-container>
    </v-main>
  </div>
</template>

<script>
import Appbar from "../components/Appbar.vue";
import { mapActions } from "vuex";

const { VUE_APP_CLIENT_ID } = process.env;

export default {
  name: "Redirect",
  components: { Appbar },
  data: () => ({
    item: "",
    token: "",
    errorMessage: "",
  }),
  computed: {},
  methods: {
    ...mapActions({
      setToken: "setToken",
      authorize: "authorize",
      incrementRequests: "loader/incrementRequests",
      decrementRequests: "loader/decrementRequests",
    }),
    redirect() {
      window.location.replace(
        `https://github.com/login/oauth/authorize?client_id=${VUE_APP_CLIENT_ID}&redirect_uri=${
          window.location.origin + (process.env.NODE_ENV === "production" ? process.env.VUE_APP_PROD_BASE_URL : "/")
        }`
      );
    },
    async getToken(item) {
      this.incrementRequests();
      try {
        await this.authorize(item);
        await this.$router.push({
          path: "/repo-list",
        });
      } finally {
        this.decrementRequests();
      }
    },
  },
  watch: {
    "$route.query.code": {
      handler: function (code) {
        if (code) this.getToken(code);
      },
      deep: true,
      immediate: true,
    },
  },
};
</script>
